.modalBackground {
    width: 100vw;
    height: 100vh;
    background-color: rgba(200, 200, 200, 0.7);
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0%;
    left: 0%;
    z-index: 10;
  }
  
  .modalContainer {
    width: 700px;
    height: 500px;
    border-radius: 12px;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    display: flex;
    flex-direction: column;
    padding: 25px;
  }

  .basketModalContainer{
    position: fixed;
    left: 30%;
    width: 70vw;
    height: 100%;
    border-radius: 12px;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    display: flex;
    flex-direction: column;
    overflow: scroll;
  }
  
  .modalContainer .title {
    display: inline-block;
    text-align: center;
    margin-top: 10px;
  }
  
  .titleCloseBtn {
    display: flex;
    justify-content: flex-end;
  }
  
  .titleCloseBtn button {
    background-color: transparent;
    border: none;
    font-size: 25px;
    cursor: pointer;
  }
  
  .modalContainer .body {
    flex: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.7rem;
    text-align: center;
  }
  
  .modalContainer .footer {
    flex: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modalContainer .footer button {
    width: 150px;
    height: 45px;
    margin: 10px;
    border: none;
    background-color: cornflowerblue;
    color: white;
    border-radius: 8px;
    font-size: 20px;
    cursor: pointer;
  }
  
  #cancelBtn {
    background-color: crimson;
  }

  .item-count-container{
    display: flex;
    justify-content: center;
  }

  .item-wrapper{
    display: flex;
  }
  
  .item-img{
    margin-right: 30px;
  }

  .item-info{
    width:100%
  }

  .item-name{
    margin-bottom: 10px;
    font-size: 18px;
    font-weight: 500;
  }

  .item-price{
    margin-bottom: 20px;
    font-size: 20px;
    font-weight: 500;
  }

  .addToBasket{ 
    color: white;
    width: 100%;
    height: 40px;
    background: #FAAD00;
    font-weight: 500;
    font-style: normal;
    font-size: 14px;
    line-height: 14px;
    border-radius: 3px;
    padding: 0 30px;
    margin-bottom: 50px;
    border: 0 none;
  }

  .item-comment-label{
    font-size: 12px;
    margin: 5px 0px;
  }

  #item-comment-body{
    width: 100%;
    border-color: rgb(159, 159, 159);
    padding: 10px 10px;
    min-height: 90px;
    line-height: 22px;
    border-radius: 5px;
  }

  .footer{
    color: rgb(155, 155, 155);
    font-size: 10px;
  }

  /*basket modal container*/


  .basketTitleCloseBtn {
    display: flex;
    height: 10%;
    min-height: 55px;
    justify-content: space-between;
    padding: 0px 20px;
    align-items: center;
    background-color: rgb(246, 246, 246);
    font-weight: 500;
    font-style: normal;
    font-size: 18px;
    line-height: 24px;
    color: rgb(48, 48, 48);
  }

  .basketTitleCloseBtn button{
    background-color: transparent;
    border: none;
  }

  .img-name-container{
    display: flex;
    align-items: center;
    width: 30%
  }

  .item-info-wrapper{
    display:flex;
    justify-content: space-between;
    padding: 10px 15px;
    align-items: center;
  }

  .basket-img-container img{
    width: 50px;
    height: 50px;
  }

  .basket-item-name-container{
    padding: 0px 5px;
  }

  .item-count-wrapper{
    display: flex;
    align-items: center;
    width:40%;
  }

  .item-totalCount{
    width: 100%;
    height: 30px;
    text-align: center;
    border: solid black 1px;
    border-radius: 3px;
    border-color: rgba(200, 200, 200);
    margin: 0px 10px;
  }

  .basket-item-control-btn{
    display: flex;
    flex-direction: column;
    width: 40px;
    height: 30px;
    border-radius: 3px;
    cursor: pointer;
    align-items: center;
    padding: 0px;
    border: 1px solid #FAAD00;
    font-weight: bold;
  }

  .add-btn{
    background-color: #FAAD00;
    color: white;
  }

  .add-btn:hover{
    background-color: white;
    color: rgb(64, 72, 68);
  }

  .subtract-btn{
    background-color: white;
    color: rgb(64, 72, 68);
  }

  .subtract-btn:hover{
    background-color: #FAAD00;
    color: white;
  }

  .basket-item-price-container{
    padding: 0px 5px;
  }

  .price-delete-btn-wrapper{
    display: flex;
    align-items: center;
  }

  .delete-btn-wrapper{
    width: 25%;
    padding: 0px 5px 3px 5px;
    border-radius: 3px;
  }

  .delete-btn-wrapper:hover{
    background-color: crimson;
    color: white;
  }

  .delete-btn{
    background-color: white;
  }
  .delete-btn:hover{
    background-color: #FAAD00;
    color: white;
  }

  .basket-modal-footer{
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    padding: 10px 20px;
    width:70%;
    height: 70px;
    background-color: rgb(246, 246, 246);
    position: fixed;
    bottom: 0;
  }

  .checkout-btn{
      color: white;
      width: 100%;
      height: 40px;
      background: #FAAD00;
      font-weight: 500;
      font-style: normal;
      font-size: 14px;
      line-height: 14px;
      border-radius: 3px;
      padding: 0 30px;
      margin-bottom: 50px;
      border: 0 none;
  }

  .hr{
    color: rgba(200, 200, 200, 0.7);
    height: 0.5px;
  }

  .customAmount{
    text-align: center;
  }


  /*Confirmation Modal*/

  .confirmation-container-hidden{
    display: none;
  }

  .confirmation-container{
    display: flex;
    align-items: center;
    text-align: center;
    color: orange;
    background-color: rgb(48, 48, 48, 0.95);
    width: 100%;
    height: 75vh;
    justify-content: center;
    font-family: 'Borsok', sans-serif;
    font-size: 56px;
  }



  @media only screen and (max-width: 700px) {
    .admin-panel-wrapper{
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    .admin-panel-button{
      width:100%; 
    }
    .admin-subpanel-wrapper{
      width: 100%;
    }
    .nav-bar-right-panel{
      display: flex;
      flex-wrap: nowrap;
    }

    .confirmation-container{
      height: 115vh;
      font-size: 2em;
    }
  }


  @media only screen and (max-width: 550px) {

    /*basketModalContainer*/

    .basket-item-name-container{
      font-size: 12px;
    }

    .basketTitleCloseBtn{
      flex-direction: column-reverse;
    }

    .basket-title{
      margin-bottom: 15px;
      position: absolute;
    }

    .closeBtnWrapper{
      margin-bottom: 15px;
    }

    .closeBtnWrapper{
      width: 100%;
      display: flex;
      justify-content: flex-end;
    }

    .item-count-container{
      padding: 5px 0px 15px 0px;
    }

    .img-name-container{
      display: flex;
      align-items: center;
      width: 70%
    }

    .item-img img{
      max-width: 200px !important;
    }

    /*prevent scrollling when modal basket is opened*/
    .root-no-scroll{
      position: fixed;
      overflow: hidden;
    }
    
    .basketModalContainer{
      left: auto;
      width: 100%;
      height: 93vh;
      bottom: 0px;
    }

    .basket-modal-final-hr{
      padding-bottom: 10%;
      color:white;
    }
    
    .basket-item-price-container{
      padding: 0px;
    }
    .basket-modal-footer{
        /* position: relative; */
        flex-direction: column-reverse;
        width: 100%;
        align-items: none;
        height: 15%;
        background-color: whitesmoke;
    }

    .confirmation-container{
      height: 75vh;
      font-size: 2em;
    }

    .confirmation-text{
    text-align: center;    
  }

  .checkout-btn-wrapper{
    width: 100%;
  }

  .checkout-btn{
    margin-bottom: 5px;
  }

  .totalAmountContainer{
    padding-bottom: 0px;
    font-size: 14px;
  }

  .item-count-wrapper{
    width: 45%;
  }

  .customAmount{
    padding: 0px;
  }

  .basket-item-control-btn{
    width: 80px;
  }

  /*item-modal*/
  .modalContainer{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 95%;
    overflow-y: scroll;
}

.item-wrapper{
    display: flex;
    flex-direction: column;
}

.item-img{
    display: flex;
    justify-content: center;
    margin: 0px;
}
.item-img.img{
    max-width: 50%
}

.item-price{
    margin-bottom: 0px;
}

.addToBasket{
    margin-bottom: 0px; 
}
  }
